#History {
    margin: 50px 48px;
}

#History #header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 52px;
}

#History #header > p {
    text-transform: uppercase;
    font-weight: 600;
    font-size: 20px;
    line-height: 23px;
    color: #241B3D;
}

#History #header button {
    display: flex;
    align-items: center;
    padding: 12px 16px;
    background-color: rgba(113, 78, 215, 1);
    border-radius: 8px;
    border: none;
    cursor: pointer;
}

#History #header button:focus {
    outline: none;
}

#header button p {
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;
    margin-left: 10px;
}

.link {
    text-decoration: none;
}

@media (max-width: 1024px) {
    #History {
        margin: 100px 48px;
    }
}

@media (max-width: 700px) {
    #History {
        margin: 40px 10px;
    }

    #History #header {
        flex-direction: column;
        margin-bottom: 40px;
    }

    #History #header > p {
        margin-bottom: 20px;
    }
}