.CardContainer {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 24px 32px;
    margin-bottom: 8px;
    box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.12);
    border-radius: 8px;
    background-color: white;
}

.CardContainer p {
    color: rgba(36, 27, 61, 1);
    font-size: 16px;
    line-height: 24px;
    margin-right: 16px;
}

.card-info {
    display: flex;
    align-items: center;
}

.card-info div {
    padding: 6px 12px;
    margin-right: 8px;
    color: rgba(91, 63, 173, 1);
    text-transform: uppercase;
    font-weight: 500;
    font-size: 13px;
    line-height: 20px;
    border: 2px solid rgba(113, 78, 215, 0.06);
    border-radius: 500px;
}

.CardContainer aside img {
    width: 20px;
    height: 20px;
    margin: 8px;
    cursor: pointer;
}

.CardContainer aside {
    position: absolute;
    display: flex;
    top: 50%;
    right: 22px;
    transform: translateY(-50%);  
    background: #FFFFFF;
    box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.14);
    border-radius: 4px;
}

@media (max-width: 700px) {
    .CardContainer {
        padding: 10px;
    }

    .card-info div {
        padding: 3px 6px;
        font-weight: 400;
        font-size: 11px;
    }
}