#flag-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}
  
#red, #orange, #yellow, #green, #blue, #purple {
    width: 100%;
    height: 100%;
    -webkit-animation: flag-sweep 0s ease-out;
            animation: flag-sweep 0s ease-out;
}

#red {
    background-color: var(--red-flag);
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s;
}

#orange {
    background-color: var(--orange-flag);
    -webkit-animation-duration: 0.45s;
            animation-duration: 0.45s;
}

#yellow {
    background-color: var(--yellow-flag);
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
}

#green {
    background-color: var(--green-flag);
    -webkit-animation-duration: 0.55s;
            animation-duration: 0.55s;
}

#blue {
    background-color: var(--blue-flag);
    -webkit-animation-duration: 0.6s;
            animation-duration: 0.6s;
}

#purple {
    background-color: var(--purple-flag);
    -webkit-animation-duration: 0.65s;
            animation-duration: 0.65s;
}

@-webkit-keyframes flag-sweep {
    0% {
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes flag-sweep {
    0% {
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

#flag-container p {
    position: absolute;
    color: white;
    text-transform: uppercase;
    font-size: 60px;
    font-weight: 900;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    display: none;
}


#signin {
    position: absolute;
    width: 100vw;
    height: 85vh;
    z-index: 1;
    background-color: white;
    border-radius: 0% 0% 50% 50% / 0% 0% 10% 10%;
    display: none;
    -webkit-animation : signinsweep 0.9s ease-out;
            animation : signinsweep 0.9s ease-out;
}

@-webkit-keyframes signinsweep {
    0% {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes signinsweep {
    0% {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@-webkit-keyframes outro-sweep {
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    100% {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
}

@keyframes outro-sweep {
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    100% {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
}


